import { AppBar , Toolbar , styled } from '@mui/material';

const Container = styled(AppBar)`
    background: #060606;
    height: 9vh;
    margin 5;
`


const Header =()=>{
    const logo ="data:image/png;base64,";

    return (
        <div>
           <Container position='static'>
                <Toolbar>
                    <img src={logo} alt='logo' style={{ width:53 }}/>
                </Toolbar>
           </Container>
        </div>
    );
}

export default Header;